<template>
    <div class="preview-container" ref ='nodeContainer'>
        <div class="preview">
            <div class="zoom" ref="Nodezoom"  @mousemove="getHuge($event)" @mouseout="outHuge($event)" >
                <img id="zoomImg" class="zoomImg" :src='zoomList[0].img' alt="" srcset="">
                <div class="huge" ref="NodeHuge"></div>
            </div>
            <div class="hugeImg" ref="nodeHugeImg"></div>
            <div class="list">
              <a href="javascript:void(0);" class="goNextImg" @click="goNextImg">
                <i class="iconfont icon-biaoqing"></i>
              </a>
              <div class="img-wrap">
                <ul class="img-ul" ref="imgUl" @mouseover="getZoomImg">
                  <li v-for="(item,index) in zoomList"><img :src="item.img" alt=""></li>
                </ul>
              </div>
              <a href="javascript:void(0);" class="goBeforeImg" @click="goBeforeImg">
                <i class="iconfont icon-dibudaohanglan-"></i></a>
            </div>
        </div>
        <div class="info">
            <div class="name">
               <img src="https://img13.360buyimg.com/devfe/jfs/t4636/72/1687629000/219/64a7daf7/58e44c0fN9f20107c.png" alt=""> 
                {{ status.productDetail.goodList?.[0].wname }}               
            </div>
            <div class="atomsphere">
                <div id="J_atmosphere_banner" class="activity-banner" style="background:url(//img30.360buyimg.com/jdg/jfs/t1/117841/7/33100/6895/63a1228dEff3ca52b/85faf5eb4ebb4d7f.png) 0 0 no-repeat"></div>

            </div>
            <div class="summary-price-wrap">
                <div class="summary-price">
                    <div class="dt">京 东 价</div>
                    <div class="dd">
                      <div class="price">
                        <span>
                            <span>¥</span>
                            <span>2399</span>
                        </span>
                        <a href="">降价通知</a>
                    </div>

                    </div>
                    

                </div>
                <div class="summary-top">
                    <div class="dt">促销</div>
                    <div class="dd">
                      <div class="activity">
                        <div class="prom">
                            <em class="activity-item">满减</em>
                            <em class="activity-info">每满2000元，可减100现金</em>
                        </div>
                    </div>

                    </div>
                   
                </div>
                <div class="summary-comment">
                    <p>累计评价</p>
                    <p class="count">10万+</p>
                </div>
                <div class="choose">
                    <div class="summary-address">
                        <div class="dt">配送至
                      
                        </div>
                        <div class="dd">
                          <Mini-address></Mini-address>
                        </div>
                    </div>
                    <div class="summary-self">
                        <div class="dt"></div>
                        <div class="dd">
                          <div class="icon-el">京东物流</div>
                          <div class="ways">
                            <a href="">预约送货</a>
                            <a href="">部分收货</a>
                            <a href="">送货上门</a>
                            <a href="">自提</a>
                          </div>
                        </div>
                    </div>
                    <div class="summary-supply">
                        <div class="dt"></div>
                        <div class="dd">
                          <div>
                            由
                            <span>京东物流</span>
                            发货，在23：00之前，预计到达时间为
                            <b>3月03日</b>

                          </div>
                        </div>
                    </div>
                    <div class="choose-attrs" v-if="status.viewColorSize">
                        <div class="choose-attr-1"  v-for="(item,index) in status.displayChoose">
                            <div class="dt">{{item['name']}}</div>
                            <div class="dd">
                                <div class="item" v-for="(secItem,secIndex) in item['value']" @click="getChooseAttr(item,secItem,index,secIndex,$event)" :data-skuId="secItem['skuId']">
                                    <a href="javascript:void(0)" clstag="shangpin|keycount|product|yanse-A9 4G黑色" :data-skuId="secItem['skuId']">
                                        <img data-img="1" :src= "productDetailImg(secIndex)" width="40" height="40" alt="A9 4G黑色">
                                        <i>{{ secItem['content'] }}</i>
                                    </a>
                                </div>
                            </div> 
                        </div>
                    </div>
                    <div class="choose-pay">
                        <div class="dt">付款方式</div>
                        <div class="dd">
                            <div class="item">
                              <a >
                                <strong>¥506.00 x 3期</strong>
                              </a>
                            </div>
                            <div class="item">
                              <a >
                                <strong>¥506.00 x 3期</strong>
                              </a>
                            </div>
                            <div class="item">
                              <a >
                                <strong>¥506.00 x 3期</strong>
                              </a>
                            </div>
                            <div class="item">
                              <a >
                                <strong>¥506.00 x 3期</strong>
                              </a>
                            </div>
                        </div>

                    </div>
                    <div class="choose-btn">
                        <div class="input-wrap">
                          <el-input-number v-model="num" :min="1" :max="10" >adasd</el-input-number>
                        </div>
                        <a class="car" @click="addBuyCar($event)">加入购物车
                            <i class="iconfont icon-chenggong animate__animated " ></i>
                            <span ref="nodeSpan">请先选着一个商品</span>
                        </a>
                    </div>
                    <div class="summary-tips">
                        <div class="dt">温馨提示</div>
                        <div class="dd">
                            <div class="item">支持七天无理由退款</div>
                        </div>
                    </div>
                      
                    </div>
            </div>

        </div>
    </div>

</template>

<script>
import Header from '@/components/Header/Header.vue'
import { useRoute, useRouter } from 'vue-router'
import {ElInputNumber} from 'element-plus'             
import { computed, onMounted,onBeforeMount, reactive, watch,ref ,isProxy} from '@vue/runtime-core'
import 'element-plus/es/components/input-number/style/css'
import MiniAddress from '@/ui-mini/address/index.vue'
import { useStore } from 'vuex'
import { toRaw } from 'vue'


export default {
    name:'goodsDetail',
    components:{ Header, ElInputNumber, MiniAddress},
    setup(){
      const router = useRouter()
      const route = useRoute()
      const store = useStore()
      const status = reactive(
                    {'viewColorSize':false,
                    'attr':[],
                    'productDetail':{},
                    'currentAttr':{
                                'shopId':'',
                                'discount':'三期分付' ,
                                'shopName':'',
                                'shopLogo':'',
                                'shopUrl':'',
                    'item':{},
                    'colorSize':{}
                                },
                    'selector':{}
       })


      const zoomList = computed(()=>{
      let list = JSON.parse(localStorage.getItem('zoomList'))
      
      ;return list})
     
      

     
      let num = ref(1)
      let imgUl = ref()
      let Nodezoom = ref()
      let NodeHuge = ref()
      let nodeHugeImg = ref()
      let nodeContainer = ref()
      let nodeSpan = ref()

      //拿到商品详细信息
      let productDetail = computed(()=>{return store.state.detail.productDetail})

      let productPrice = computed(() => {return store.state.detail.productPrice })

      let product = computed(()=>{return store.state.detail.product})

      let pageDetail = computed(()=>{console.log('page cumputed');;return store.getters.doneShop})



      onBeforeMount(() => { 
        //获取商品数据
        new Promise(detailProductData)

        getPageDetail()

        //获取地址列表
        
       })

    onMounted(() => { 
        //获得放大镜背景图片后挂在页面
        let src = getHugeImgSrc(zoomList.value[0].img)
        nodeHugeImg.value.style.backgroundImage = `url(${src})`
     })
      
     
      //判断数据，然后渲染dom
      watch(product,() => {
   
        if(product.value.colorSize){
          status.viewColorSize = true

          status.attr.push({'tittle':'颜色与尺寸','list':product.value.colorSize}) 

          //将数据分类
          let p_list = toRaw(product.value.colorSize)
          let tmp = []
          for(let key in p_list[0]){
			if(key !=='skuId'){
				let attr_1 = {
				'name':key,
				'value':[],
				'choosed':false,
				}
				for(let item of p_list){
					let judge_one =  attr_1.value.find((el) => { return el['content'] ==item[key] })
					if(!judge_one){
						attr_1.value.push({'content':item[key],'display':'able'})
					}

				}
				tmp.push(attr_1)
			}
          }

		  status.displayChoose = tmp

        }
        
      
      })
      watch(productDetail,() => {
        if(productDetail.value.code ==200){
            status.productDetail['goodList'] = productDetail.value.shopRec.goodList
            // status.currentAttr.item = {...status.currentAttr.item,...status.productDetail.goodList[0]}
        }
      })
      watch(pageDetail,()=>{
     
        if(pageDetail){
            status.currentAttr.shopId = pageDetail.value.shopId
            status.currentAttr.shopName = pageDetail.value.name
            status.currentAttr.shopUrl = pageDetail.value.shopUrl
            status.currentAttr.shopLogo = pageDetail.value.shopLogo
			status.currentAttr.imageurl = zoomList.value[0].img
			
        }
      })

      watch(productPrice,() => { if(productPrice.value){
        status.currentAttr.item.price = productPrice.value[0].p
      } })
      //获取路由跳转的信息，并发送给服务器获取相应的数据

      //获取商品数据
     
      function detailProductData(resolve,reject) { 
        let sku = route.query.searchKey
        console.log(route.query);
        let url = 'https://item.jd.com/'+ sku + ".html"
        store.dispatch('getDetail',{'url':url}).then((value) => { console.log(value,'所有的action都已执行完毕'); })
       
      }
      //获取页面数据
      function getPageDetail(){
        store.dispatch('getPageDetail')
      }
      

     

      //轮播图控制左右移动的距离，数据未绑定
      function goNextImg(){
        imgUl.value.style.transform = "translate(0px,0px)"
     
      }
      function goBeforeImg(){
        imgUl.value.style.transform = "translate(-300px,0px)"
      }

      //通过图片列表获得镜头图片
      function getZoomImg($event){
       let e = $event||window.event


       if(e.target.getAttribute("src")){
        let src = e.target.getAttribute("src")
        //获得窗口图片
        Nodezoom.value.firstElementChild.setAttribute("src",e.target.getAttribute("src"))
        //获得大图图片
        nodeHugeImg.value.style.backgroundImage = `url(${getHugeImgSrc(src)})`
       }
      }

      //放大镜跟随
      function getHuge($event){
        let e = $event||window.event
        nodeHugeImg.value.classList.add("active")
        NodeHuge.value.classList.add('active')
        if(!nodeHugeImg.value.style.backgroundImage){


        }
        //获得鼠标距离容器的坐标
        let x = e.clientX - nodeContainer.value.offsetLeft 
        let y = e.clientY - nodeContainer.value.offsetTop
      
        //边界值的判断
        if(x<=NodeHuge.value.offsetWidth/2){
           x = NodeHuge.value.offsetWidth/2
        }else if(x>=Nodezoom.value.offsetWidth-NodeHuge.value.offsetWidth/2){
           x = Nodezoom.value.offsetWidth - NodeHuge.value.offsetWidth/2
        }
        if(y<=NodeHuge.value.offsetHeight/2){
           y = NodeHuge.value.offsetHeight/2
        }else if(y>=Nodezoom.value.offsetHeight-NodeHuge.value.offsetHeight/2){
           y = Nodezoom.value.offsetHeight-NodeHuge.value.offsetHeight/2
        }
    
     
        NodeHuge.value.style.left = x - 100 +"px" 
        NodeHuge.value.style.top = y - 100 + "px"
        nodeHugeImg.left = -2*(x-100) + "px"
        nodeHugeImg.top = -2*(y-100) + "px"

        nodeHugeImg.value.style.backgroundPosition = `${nodeHugeImg.left } ${nodeHugeImg.top}`
        // console.log(nodeHugeImg.value.style.backgroundPosition);

      }

      //移除放大镜
      function outHuge($event){
        let e = $event||window.event
        nodeHugeImg.value.classList.remove("active")
        NodeHuge.value.classList.remove('active')

      }
      //获得大图图片src方法
      function getHugeImgSrc(src){
        console.log('放大图片的地址',src);
        let arr = src.split("/")
        arr[3] = "n0"
        arr[4] = "jfs"
        let bigImgSrc = arr.join("/")

        return bigImgSrc

      }


	
	
       //选择商品
      function getChooseAttr(item,secItem,index,secIndex,$event){
        secItem = toRaw(secItem)
		item = toRaw(item)

		//数据确认该类别被选中=
		status.displayChoose[index].choosed = true
	
		item.value.forEach((selfItem,selfIndex)=>{
			if(selfIndex !==secIndex){
				selfItem.display = 'unable'
			}
		})
		

		//根据拿到的选择确认产品号，然后得到其他可选版本，这里是过滤列表，
		let content = secItem['content']
		
		let rawSelector = toRaw(status.selector)

		rawSelector[item['name']] = secItem['content']

		let spliter_goodList = status.attr[0].list

		for(let value in rawSelector){
			
			spliter_goodList = spliter_goodList.filter((el) => {
				let tmp_arr= Object.values(el)
				let judge = tmp_arr.includes(rawSelector[value])
				if(judge){return true}
				else{
					return false
				}
		     })

		}

		//!!这里解构的spliter是个数值。放进去是个对象，需要把对象也结构了
		status.currentAttr.item = {'price':status.currentAttr.item.price,...spliter_goodList}

		if(content =='颜色'){
			status.currentAttr.item.imageurl = zoomList.value[0].img
		}


		//
    
        $event.stopPropagation()
        let el= $event.composedPath().find((element) => { return /item/.test(element.className)})
		console.log(el,$event.composedPath(),'default el ');
        let childrenList = Array.from(el.parentElement.children)
        childrenList.forEach((item,index,arr) => { 
            item.classList.remove('choosed')
         })
        el.classList.add('choosed')

        //2023/3/29 ，这里是想根据传进来的item直接获取list，别的一遍遍输入，proxy没有拿到
        // for(let index of status.productDetail.goodList){
        //   let arr = status.productDetail.goodList
        //   if(arr[index].skuId ==item.skuId){status.currentAttr.item['wname'] = arr[index].wname}
        // }
        //遍历价格列表，拿到当前选择的价格，没拿到的情况则默认
        for(let value of productPrice.value){
          console.log(value,value.id,value['id']);
          let tmp = value.id.split('_')
          if(secItem['skuId']=tmp[1]){
            status.currentAttr.item.price = value.p
          }
        }

        //这里自定义属性会将驼峰式命名给忽略

        status.currentAttr.colorSize[String(item['name'])] = secItem['content']
		status.currentAttr.item.wname = status.productDetail.goodList[secIndex].wname ||status.productDetail.goodList[0].wname
        console.log(status.currentAttr.item);


        nodeSpan.value.style.display = 'none'
      }
      
    
      //添加点击购物车事件，采用定时器节流
      function throttle() {
   
         var timeOut

          function addData($event){ 
			//如果列表有未选中则不执行加入购物车
			let judge  = status.displayChoose.find((el) => { return el.choosed ==false })
            if(!judge){   
				//该部分为节流代码
                if(!timeOut){
               
                        status.currentAttr.item.num = num
                        if(localStorage.getItem('buyCarList')){
                            let oldValue = JSON.parse(localStorage.getItem('buyCarList'))
                            //在旧数组前添加数据在前面
                            oldValue.unshift(status.currentAttr)

                            let newValue = JSON.stringify(oldValue)

                            localStorage.setItem('buyCarList',newValue)

                        }else{
                            //数组不存在时，将建立localStorage
                            let value = JSON.stringify([status.currentAttr])

                            localStorage.setItem('buyCarList',value)
                            
                        }
                        
                        $event.target.firstElementChild.classList.add('animate__fadeInUp')
                
                      
                        timeOut =  setTimeout(()=>{
                                        timeOut = null
                                        $event.target.firstElementChild.classList.remove('animate__fadeInUp')
                                        
                                    },3000)

                }
            }else{
                console.log('请先选着一个商品');
                nodeSpan.value.style.display = 'block'
            
            }
        }
         return addData
        } 

    let addBuyCar = throttle()
   
    //根据传入的商品号在别的请求数据中拿到图片
    function productDetailImg(index){

        if(status.productDetail.length!==0){

            let list = toRaw(status.productDetail.goodList)
            if(list){
                if(/360buyimg/.test(list[0]['imageurl'])){
                   return list[0]['imageurl']
                 }else{
                        return '//img13.360buyimg.com/n9/s40x40_' + `${list[index].imageurl}`
                 }
            }
        }
    }

   

      return{
        num,
        imgUl,
        zoomList,
        pageDetail,
        status,
        goNextImg,
        goBeforeImg,
        getZoomImg,
        getHuge,
        outHuge,
        getHugeImgSrc,
        getChooseAttr,
        addBuyCar,
        productDetailImg,
        Nodezoom,
        NodeHuge,
        nodeSpan,
        nodeHugeImg,nodeContainer,
        product,
        productDetail,
        productPrice
      }
    }

  }
</script>

<style lang="scss" scoped>
@use '@/scss/index.scss';

@mixin clearfix{
  content:'';
  clear:both;
  display: block;
  height: 0;
  visibility: hidden;


}

@mixin dt{
  float: left;
  width: 50px;
  color: #999;
  font-size:12px;
  line-height: 22px;

}
@mixin dd{
  // float: left;
  color: #666;
  font-size:12px;
  line-height: 22px;
  margin-left:70px;

}
@mixin choose-item{
  float: left;
  background-color: #f7f7f7;
  margin-right: 7px;
  margin-bottom:4px;
  border: solid 1px #ccc;
  font-size: 12px;
          
}
$price-color:#e4393c;
// ::v-deep{
//   .el-input-number{
//     width: 200px;
//     height: 300px;
//   }
// }
Mini-address{
      float: left;
    }
.preview-container{
    width:1210px ;
    margin: 0 auto;
    padding: 0;
    background-color: #fff;

}
.preview {
    float: left;
    width: 600px;
    margin: 0 ;
    padding: 0; 
    position: relative;
    .hugeImg{
      display: none;
      position: absolute;
      top: 0;
      left: 450px;
      width: 400px;
      height: 400px;
      background: url(//img14.360buyimg.com//n0/jfs/t1/199026/10/28394/40012/63515484E66484406/03d6ad1ddff4d7ce.jpg.avif) no-repeat;
      z-index: 6;
    }
    .hugeImg.active{
      display: block;
    }
}
.preview .zoom{
    width: 450px;
    margin: 0 ;
    padding: 0;
    position: relative;

    .zoomImg{
      width:100%

    }
    .huge{
      top: 0;
      left: 0;
      position: absolute;
      width: 200px;
      height: 200px;
      background:50% top no-repeat #fede4f;
      display: none;
    }
     .huge.active{
      display: block;
      top: 0;
      left: 0;
      position: absolute;
      width: 200px;
      height: 200px;
      background-color:50% top no-repeat #fede4f;
      opacity: .5;
      cursor: move;


    }
}
.preview .list{
    width: 500px;
    height: 200px;
    overflow: hidden;
    position: relative;
    .iconfont{
      font-size: 30px;
    }
    .goNextImg{
      float: left;
      width: 20px;
      height: 30px;
      position: absolute;
      left: 0;
      top: 40px;
      overflow: hidden;

    }
    .goBeforeImg{
      right:0;
      top: 40px;
      width: 40px;
      height: 30px;
      
      position: absolute;
      overflow: hidden;
   

    }
    .img-wrap{
      width: 400px;
      height: 100px;
      margin: 0 50px;
      overflow: hidden;
      position: relative;
    }
    &::after{
      @include clearfix()
    }
}


.preview .list ul{
  padding: 0;
  margin: 0;
  list-style: none;
  width: 1000px;
  height: 100px;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0px;
  transition: transform 500ms;
  &::after{
      @include clearfix()
      
    }

}
.preview .list ul li {
    float: left;
    box-sizing: border-box;
    width: 100px;
    height: 100px;
   
    &:hover{
      border: solid 1px red;
    }


}
.preview .list ul li img{
    width: 100%;
    display: block;
}

.info{
    float:right;
    width: 610px;
    overflow: hidden;
}
.preview-container::after{
    content: "";
    clear: both;
    display: block;

}
.activity-banner{
  height: 32px;
}
.info .name{
  font-size: 16px;
}
.info .summary-price-wrap{
  position: relative;
}
.info .summary-price-wrap {
  .summary-top{
    height: 30px;
    margin-bottom: 10px;

    .dt{
      @include dt;
      
    }
    .dd{
      @include dd;
 
      color:white;
      background-color: #e4393c;
      font-weight: 200;

    }
  }
  
  .choose{
  

    
    .summary-address{
      height: 20px;
      margin-bottom: 10px;
   
       .dt{
        @include dt;
       }
       .dd{
        @include dd;
        .show-address{
          border: solid 1px #ccc;
          float: left;
    

        }


       }
    }
    .summary-supply{
      margin-bottom: 10px;
   
       .dt{
        @include dt;
       }
       .dd{
        @include dd;

        height: 25px;

       }
    }
    .summary-self{
      @extend .summary-supply;
      .dd{
        .icon-el{
          display: inline-block;
          margin-right: 10px;

        }
        .ways{
          display: inline-block;
        }
      }
    }
    .choose-attrs{
       .choose-attr-1{
        margin-bottom: 10px;
  
    
        .dt{
          @include dt;
        }
        .dd{
          @include dd;
          overflow: hidden;
        .item{
          @include choose-item;
          border: solid 1px #ccc;
          font-size: 12px;
          a{
             display: block;
			 text-decoration: none;
			 width: 100px;
			 overflow: hidden;
             img{
               width: 30px;
               height: 30px;
               border: 0;
               vertical-align: middle;
              }
             i{
			  margin:0 5px ;
              color:black;
             }
           }
           &.choosed{
            border: solid 1px red;
           }

        }
        &:after{
          content: "";
          display: block;
          height: 0;
          clear: both;
          visibility: hidden;

        }

        }
